export default {
	data() {
		return {
			userInfo: {
				clientNo: null,
				userName: '未登录',
				avaterUrl: 'http://a3q.dns06.net.cn/15844990493147.jpeg'
			},
			vipInfo: {
				
			},
			synopsis: "",
			levelpercent: "",
			loading:false,
			acctDetail: {},
			pay: 999,
			income: 555,
			isLogin: false,
			assets: 100000,
			lastIncome: -25,
			userLevel: {},
			messageList: [{
				cuIcon: 'redpacket',
				color: 'alive',
				number: '0',
				badge: 0,
				url: "/pages/bank_card/index/index",
				name: '微博'
			}, {
				cuIcon: 'refund',
				color: 'alive',
				number: '0',
				badge: 0,
				name: '关注'
			}, {
				cuIcon: 'present',
				color: 'alive',
				number: '0',
				badge: 0,
				name: '粉丝'
			}],
			gridCol: 3,
		}
	},
	onShow() {
		console.log(uni.getStorageSync('userId'))
		console.log(this.isLogin)
		console.log(1)
		let _this = this;
		// 获取用户信息
		if(uni.getStorageSync('isLogin') != "" || uni.getStorageSync('isLogin')!= null){
			_this.isLogin = uni.getStorageSync('isLogin');
		}
		if(uni.getStorageSync('userName') != ""){
			_this.userInfo.userName = uni.getStorageSync('userName')
		}else{
			_this.userInfo.userName = "未登录"
		}
		if(uni.getStorageSync('synopsis') != ""){
			_this.synopsis = uni.getStorageSync('synopsis')
			if(_this.synopsis.length > 8){
				_this.synopsis = _this.synopsis.substr(0,8) + "..."
			}
		}else{
			_this.synopsis = ""
		}
		console.log(_this.isLogin)
		console.log("获取关注信息")
		uni.request({
			url: this.Server_IP + 'followIm', //仅为示例，并非真实接口地址。
			data: {
				fansId: "",
				userId: uni.getStorageSync('userId')
			},
			header: {
				'custom-header': 'followIm' //自定义请求头信息
			},
			method:"POST",
			dataType:"json",
			success: (res) => {
				console.log(res.data);
				if(res.data.info.code == '0'){
					_this.messageList[1].number = +res.data.data.follow;
					_this.messageList[2].number = +res.data.data.fans;
					console.log("获取关注粉丝成功")
				}else{
					console.log("获取关注粉丝失败")
				}
			},
			fail() {
				console.log("获取关注粉丝失败")
			}
		});
		uni.request({
			url: this.Server_IP + 'sayNum', //仅为示例，并非真实接口地址。
			data: {
				userId: uni.getStorageSync('userId')
			},
			header: {
				'custom-header': 'sayNum' //自定义请求头信息
			},
			method:"POST",
			dataType:"json",
			success: (res) => {
				console.log(res.data);
				if(res.data.info.code == '0'){
					_this.messageList[0].number = +res.data.data.num;
					console.log("获取微博数量成功")
				}else{
					console.log("获取微博数量失败")
				}
			},
			fail() {
				console.log("获取微博数量失败")
			}
		});
		console.log("获取完成")
	},
	methods: {
		change() {
			let _this = this;

			if(uni.getStorageSync('isLogin')){
				uni.navigateTo({
					url: '/pages/personal/information'
				});					
			}else{
				uni.navigateTo({
					url: '/pages/login/login'
				});					
			}
			
		},
		toList(name){
			if(name == "微博"){
				uni.request({
					url: this.Server_IP + 'allsay', //仅为示例，并非真实接口地址。
					data: {
						userId: uni.getStorageSync('userId')
					},
					header: {
						'custom-header': 'allsay' //自定义请求头信息
					},
					method:"POST",
					dataType:"json",
					success: (res) => {
						console.log(res.data);
						if(res.data.info.code == '0'){
							console.log(res.data)
							uni.navigateTo({
								url: '/pages/subscriber/user_say?title=我的微博&sayList='+res.data.data.sayList
							});	
						}else{
							console.log("获取微博失败")
						}
					},
					fail() {
						console.log("获取微博失败")
					}
				});
				
				
			}
			if(name == "关注"){
				uni.request({
					url: this.Server_IP + 'queryfollow', //仅为示例，并非真实接口地址。
					data: {
						userId: uni.getStorageSync('userId')
					},
					header: {
						'custom-header': 'queryfollow' //自定义请求头信息
					},
					method:"POST",
					dataType:"json",
					success: (res) => {
						console.log(res.data);
						if(res.data.info.code == '0'){
							uni.navigateTo({
								url: '/pages/subscriber/user_list?title=我的关注&userList='+res.data.data.follow
							});	
						}else{
							console.log("获取关注粉丝失败")
						}
					},
					fail() {
						console.log("获取关注粉丝失败")
					}
				});
			}
			if(name == "粉丝"){
				uni.request({
					url: this.Server_IP + 'queryfans', //仅为示例，并非真实接口地址。
					data: {
						userId: uni.getStorageSync('userId')
					},
					header: {
						'custom-header': 'queryfans' //自定义请求头信息
					},
					method:"POST",
					dataType:"json",
					success: (res) => {
						console.log(res.data);
						if(res.data.info.code == '0'){
							uni.navigateTo({
								url: '/pages/subscriber/user_list?title=我的粉丝&userList='+res.data.data.fans
							});	
						}else{
							console.log("获取关注粉丝失败")
						}
					},
					fail() {
						console.log("获取关注粉丝失败")
					}
				});
			}
		},
		changeImage () {
			uni.navigateTo({
				url: "/pages/personal/change_headpic"
			})
		},
		write(){
			uni.navigateTo({
				url: "/pages/write/write"
			})
		}
	},
}
